<template>
    <div class="homepage">
        <div class="bread_crumb">
            <!--<span>位置：主页 | </span>-->
            <span>当前位置：</span>
            <span>库存管理</span>
        </div>
        <div class="stock_title">
            <span>库存物品基础信息</span>
            <div class="search_input el-input el-input--suffix">
                <input autocomplete="off" v-model="stockInputValue" placeholder="请输入要搜索的物品名称" type="text" rows="2" validateevent="true"
                       suffixicon="el-icon-search" class="p_search el-input__inner" @keyup.enter.active="searchStockClick()">
                <span class="el-input__suffix" @click="searchStockClick()">
                    <span class="el-input__suffix-inner">
                        <i class="el-input__icon iconfont el-icon-pharmaceutical_administration-sousuo"></i>
                    </span>
                </span>
            </div>
            <el-select class="select_input" v-model="stockOptionsValue" @change="stockChange()">
                <el-option v-for="item in stockSearchOptions"
                        :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
        </div>
        <!--基本信息-->
        <el-table class="basic_table" v-if="" :data="basicData" stripe style="width: 100%">
            <el-table-column prop="id" label="大类编号"></el-table-column>
            <el-table-column prop="pic" label="大类图片">
                <template slot-scope="scope">
                    <img v-if="stockOptionsValue == 1" src="../../static/pic_yaoji.png" alt="">
                    <img v-if="stockOptionsValue == 2" src="../../static/pic_yiqi.png" alt="">
                </template>
            </el-table-column>
            <el-table-column prop="sort_name" label="大类名称"></el-table-column>
            <el-table-column prop="stock" label="总量">
                <template slot-scope="scope">
                    <span>{{ scope.row.stock }}</span><span v-if="stockOptionsValue == 1">瓶</span><span v-if="stockOptionsValue == 2">个</span>
                </template>
            </el-table-column>
            <!--<el-table-column prop="unit" label="单位" width="180"></el-table-column>-->
            <el-table-column label="操作" width="180">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleDetail(scope.$index, scope.row)">查看详情</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="block" style="text-align: center;margin-top: 30px;">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="pageIndex"
                    :page-size="10"
                    layout="total, prev, pager, next, jumper"
                    :total="pageTotal">
            </el-pagination>
        </div>
    </div>
</template>

