				basicData: [],
				pageIndex: 1,       //第几页
				pageTotal: 0,
			}
		},
		created(){
            this.getAllList();
            console.log(this.$store.state.protocol)
            console.log(this.$store.state.host)
		},
		mounted(){
		    //使select的input的光标消失（IE  360兼容模式）
		    let inputs = document.getElementsByClassName('select_input')[0].childNodes[1].children[0]
            inputs.setAttribute("unselectable", "on");
		},
		methods:{
			//获取大类库存列表（现在是被上架的物品列表）
            getAllList(){
				let item_type = '';
				if(this.stockOptionsValue == 1){
					item_type = '药品试剂';
				} else if(this.stockOptionsValue == 2){
					item_type = '仪器设备';
				}
            	this.$axios.post('/sort-big',{
            		item_type: item_type,
                    number: 10,
                }).then((response)=>{
//            		console.log(response);
                    if(response.data.data){
						this.basicData = response.data.data.data;
						this.pageTotal = response.data.data.total;
                    }
            		for(let i in this.basicData){
            			if(this.basicData[i].photo == null){
            				this.basicData[i].pic = null
                        } else {
            				this.basicData[i].pic = this.basicData[i].photo.split('/')[1]
                        }
                    }
                }).catch((error)=>{})
            },
			//选择类型
			stockChange(){
				this.stockInputValue = '';
				this.pageIndex = 1;
				let item_type = '';
				if(this.stockOptionsValue == 1){
					item_type = '药品试剂';
				} else if(this.stockOptionsValue == 2){
					item_type = '仪器设备';
				}
				this.$axios.post('/sort-big',{
					item_type: item_type,
					number: '10',
				}).then((response)=>{
//					console.log(response)
                    if(response.data.data){
						this.pageTotal = response.data.data.total;
						this.basicData = response.data.data.data;
						for(let i in this.basicData){
							if(this.basicData[i].photo == null){
								this.basicData[i].pic = null
							} else {
								this.basicData[i].pic = this.basicData[i].photo.split('/')[1]
							}
						}
                    } else {
						this.pageTotal = 0;
						this.basicData = [];
                    }
//					console.log(this.basicData)
				})
			},
            //搜索
			searchStockClick(){
				let item_type = '';
				this.pageIndex = 1;
				if(this.stockOptionsValue == 1){
					item_type = '药品试剂';
				} else if(this.stockOptionsValue == 2){
					item_type = '仪器设备';
				}
				this.$axios.post('/sort-big',{
					item_type: item_type,
					number: '10',
					sort_name: this.stockInputValue
				}).then((response)=>{
//					console.log(response)
					if(response.data.data){
						this.pageTotal = response.data.data.total;
						this.basicData = response.data.data.data;
						for(let i in this.basicData){
							if(this.basicData[i].photo == null){
								this.basicData[i].pic = null
							} else {
								this.basicData[i].pic = this.basicData[i].photo.split('/')[1]
							}
						}
					} else {
						this.pageTotal = 0;
						this.basicData = [];
					}
				})
            },
			//分页
			handleCurrentChange(val) {
//				console.log(`当前页: ${val}`);
				let item_type = '';
				if(this.stockOptionsValue == 1){
					item_type = '药品试剂';
				} else if(this.stockOptionsValue == 2){
					item_type = '仪器设备';
				}
				this.$axios.post('/sort-big',{
					item_type: item_type,
					number: '10',
                    page: val,
                    sort_name: this.stockInputValue
				}).then((response)=>{
//					console.log(response)
					if(response.data.data){
						this.pageTotal = response.data.data.total;
						this.basicData = response.data.data.data;
						for(let i in this.basicData){
							if(this.basicData[i].photo == null){
								this.basicData[i].pic = null
							} else {
								this.basicData[i].pic = this.basicData[i].photo.split('/')[1]
							}
						}
					} else {
						this.pageTotal = 0;
						this.basicData = [];
					}
				})
			},
			//基本信息 点击详情
			handleDetail(index, row) {
//				console.log(index, row);
				let item_type = '';
				if(this.stockOptionsValue == 1){
					item_type = '药品试剂';
					this.$router.push('/drug/' + row.id)
				} else if(this.stockOptionsValue == 2){
					item_type = '仪器设备';
                    this.$router.push('/instrument/' + row.id)
				}
			},
		},
