</script>

<style lang="less" rel="stylesheet/less">
    .homepage{
        background-color: #ffffff;
        padding: 17px 24px 30px 24px;
        //面包屑
        .bread_crumb{
            height: 22px;
            font-family: PingFangSC;
            font-size: 16px;
            text-align: left;
            color: #223241;
            margin-bottom: 22px;
        }
        //顶部信息
        .stock_title{
            margin-bottom: 15px;
            >span{
                height: 20px;
                font-family: PingFangSC;
                font-size: 14px;
                text-align: left;
                color: #223241;
            }
            .el-input.search_input{
                width: 366px;
                height: 30px;
                border-radius: 13px;
                margin-left: 20px;
                .el-input__inner{
                    background-color: #f1f1f3;
                    height: 30px;
                    border-radius: 20px;
                    line-height: 30px;
                    border: 0;
                }
                .el-input__icon{
                    line-height: 30px;
                }
                .el-input__suffix {
                    i{
                        &:before {
                            border-radius: 20px;
                            display: block;
                            width: 25px;
                            height: 25px;
                            line-height: 25px;
                            font-size: 16px;
                            background-color: #5ca8e7;
                            color: #ffffff;
                            text-align: center;
                            margin-top: 2.5px;
                            font-weight: bold;
                            cursor: pointer;
                        }
                    }
                }
            }
            .el-select{
                width: 106.9px;
                margin-left: 15px;
                .el-input__inner{
                    background-color: #5ca8e7;
                    color: #fff;
                    border-radius: 22px;
                    padding-left: 16px;
                    font-size: 12px;
                    height: 30px;
                    line-height: 30px;
                }
                .el-input .el-select__caret{
                    color: #fff;
                }
            }
            .right_btn.el-button--primary{
                float: right;
                width: 87px;
                height: 30px;
                border-radius: 5px;
                background-color: #5abfac;
                border-color: #5abfac;
                padding: 8px 20px;
                margin-left: 23px;
            }
        }
        //表格
        .el-table{
            //底线去除
            td,th.is-leaf{
                border-bottom: 0;
            }
            thead{
                color: #fff;
                th{
                    font-weight: normal;
                    background-color: #223241;
                    padding: 11px 0;
                    div.cell{
                        line-height: 19px;
                    }
                }
            }
            tr{
                td{
                    padding: 0;
                    div.cell{
                        line-height: 42px;
                        height: 42px;
                    }
                }
            }
            //多选
            .el-checkbox__inner{
                border-radius: 20px;
            }
            img{
                width: 71px;
                height: 30px;
                margin-top: 6px;
            }
        }
        //基本表格
        .basic_table{
            td,th.is-leaf{
                border-bottom: 0;
            }
            .el-button{
                border: 0;
                padding: 0;
                background: transparent;
                font-size: 14px;
                text-align: left;
                color: #5ca8e7;
            }
            //大类编号
            .has-gutter {
                tr {
                    th:first-child {
                        padding-left: 50px;
                    }
                }
            }
            tbody {
                tr {
                    td:first-child {
                        padding-left: 50px;
                    }
                }
            }
        }
    }
</style>


